.contacts {
  padding: 114px 0 123px 0;
  &__block {
  }
  &__item {
    max-width: 342.18px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    & svg {
      cursor: pointer;
    }
  }
  .title {
    margin-bottom: 25px;
  }
  .subtitle {
    margin: 0 auto 29px auto;
    max-width: 275px;
  }
  &__btns {
    margin: 30px auto 86px auto;
    max-width: 185px;
  }
  &__btn {
    border-radius: 50px;
    background: #070707;
    padding: 13px 30px;
    font-family: "Gilroy-Bold", sans-serif;
    font-size: 18px;
    line-height: 22px;
    text-align: center;
    color: #ffffff;
  }

  &__subtext {
    margin-top: 45px;

    font-family: "Gilroy-Medium";
    font-size: 14px;
    line-height: 17px;
    text-align: center;
    color: #828282;
  }
}
