.main {
  padding: 40px 0 104px 0;

  &__image {
    &__mobile {
      display: none;
    }
    & img {
      width: 100%;
    }
  }
  &__block {
    margin-bottom: 30px;
    display: flex;
    justify-content: space-between;
  }
  &__title {
    font-family: "Gilroy-Bold", sans-serif;
    font-size: 47px;
    line-height: 59px;
    color: #070707;
    max-width: 123.51px;
  }
  &__subtitle {
    font-family: "Gilroy-Medium", sans-serif;
    font-size: 18px;
    color: #070707;
    max-width: 155.65px;
  }
  &__text {
    margin-bottom: 30px;
    font-family: "Gilroy-Bold", sans-serif;
    font-size: 16px;
    line-height: 20px;
    transform: rotate(-90deg);
    & button {
      color: #828282;
      border: none;
      background: transparent;

      &:hover {
        cursor: pointer;
      }
      &:disabled {
        color: #070707;
      }
    }
  }
}
