@media screen and (max-width: 1024px) {
  #updated-block {
    display: none;
  }
}

.mobile-menu {
  display: none;

  &__link {
    & a {
      cursor: pointer;
      font-family: "Gilroy-Bold", sans-serif;
      font-size: 34px;
      line-height: 42px;
      text-decoration: none;
      color: #828282;
    }
  }
}

@media screen and (max-width: 768px) {
  .skills {
    &__block {
      flex-wrap: wrap;
    }
  }

  .header {
    display: none;
  }

  .main {
    padding: 0;
    &__image {
      margin-bottom: 83px;
    }
    &__block {
      margin-top: 65px;
    }
  }

  .mobile-menu {
    display: block;

    &__burger {
      font-family: "Gilroy-Bold", sans-serif;
      position: absolute;
      font-weight: 900;
      top: 32px;
      right: 30px;
      font-size: 33px;
      color: #000;
    }

    &__link {
      padding: 34px 0;
      & a {
      }
    }

    &__cancel {
      color: #828282;
      cursor: pointer;
      font-family: "Gilroy-Bold", sans-serif;
      font-size: 28px;
    }

    &__pos {
    }

    &__cancel {
      position: absolute;
      right: 28px;
    }

    &__list {
      display: flex;
      position: fixed;
      width: 100%;
      height: 100%;
      flex-direction: column;
      top: 0;
      left: 0;
      margin: 0;
      background: #fff;
      z-index: 3;
      text-align: left;
      padding: 66px 26px 57px 26px;
      color: #ffffff;
    }
  }

  .main__text1 {
    position: absolute;
    right: 0;
    font-family: "Gilroy-Bold", sans-serif;
    font-size: 34px;
    transform: rotate(90deg);
    & button {
      color: #828282;
      border: none;
      background: transparent;

      &:hover {
        cursor: pointer;
      }
      &:disabled {
        color: #070707;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .skills {
    &__block {
      margin-top: 0;
    }
    &__item {
      margin-top: 81px;
    }
  }
}

@media screen and (max-width: 426px) {
  .skills {
    padding: 83px 0 98px 0;
  }

  .portfolio {
    padding: 81px 0;
  }
  .contacts {
    padding: 82px 0 90px 0;
  }

  .about {
    padding: 81px 0 76px 0;
  }
  .main {
    &__image {
      display: none;
      &__mobile {
        display: block;
        & img {
          width: 100%;
        }
      }
    }

    &__subtitle {
      margin-top: 23px;
      margin-bottom: 37px;
      max-width: 155.65px;
    }

    &__block {
      display: block;
    }

    &__text {
      display: none;
    }
    &__text1 {
      position: absolute;
      display: block;
      font-family: "Gilroy-Bold", sans-serif;
      font-size: 16px;
      line-height: 20px;
      transform: rotate(-90deg);
    }
  }

  .mobile-menu {
    &__burger {
      top: 74px;
    }
  }

  .main__text1 {
    position: absolute;
    right: 0;
    font-family: "Gilroy-Bold", sans-serif;
    font-size: 34px;
    transform: rotate(90deg);
    & button {
      color: #828282;
      border: none;
      background: transparent;

      &:hover {
        cursor: pointer;
      }
      &:disabled {
        color: #070707;
      }
    }
  }
}
