.skills {
  padding: 91px 0 110px 0;

  &__image {
    max-width: 89.21px;
    margin: 0 auto;
    text-align: center;
  }

  &__cont {
    width: 100%;
    margin: 30px auto 0 auto;
  }

  &__item {
    margin: 0 auto;
    width: 166.46px;
  }

  &__block {
    margin-top: 84px;
    display: flex;
    justify-content: space-between;
  }

  &__title {
    height: 80px;
    margin: 0 auto;
    margin-top: 23px;
    max-width: 80px;
    font-family: "Gilroy-Bold", sans-serif;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
    color: #828282;
  }

  &__rate {
    display: flex;
    justify-content: center;
    // margin-top: 40px;
  }
}
