.header {
  &__block {
    border-bottom: 1px solid #828282;
    transform: matrix(1, 0, 0, 1, 0, 0);
    display: flex;
    justify-content: space-between;
    padding: 32px 0;
  }
  &__link {
    text-decoration: none;
    font-family: "Gilroy-Bold", sans-serif;
    font-size: 18px;
    line-height: 22px;
    color: #828282;
    &:hover {
      color: #070707;
    }
  }
}
